import React, { useEffect, useState } from "react";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import BarChartIcon from "@mui/icons-material/BarChart";
import SettingsApplicationsIcon from "@mui/icons-material/SettingsApplications";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import AddCircleIcon from "@mui/icons-material/AddCircle";

function Header() {
  const [timer, setTimer] = useState(25);
  const [isStartClicked, setStartClicked] = useState(false);
  const [countdownTime, setCountdownTime] = useState(timer * 60);
  const switchToNextTimer = () => {
    // Define your timer transitions here
    const timerTransitions = {
      25: 5, // Pomogulcan to Short Break
      5: 25, // Short Break to Pomogulcan
      // Add more transitions as needed
    };
    if (timer === 25) {
      setHowManyPomo((prevCount) => prevCount + 1); // Increment howManyPomo for Pomogulcan
      if (howManyPomo === 4) {
        setTimer(15); // Set timer to Long Break when howManyPomo reaches 4
        setCountdownTime(15 * 60);
        setHowManyPomo(1); // Reset howManyPomo
        return;
      }
    }
    setTimer(timerTransitions[timer] || 25); // If the next timer is not defined, default to Pomogulcan
    setCountdownTime((timerTransitions[timer] || 25) * 60);
  };

  const [howManyPomo, setHowManyPomo] = useState(1);

  useEffect(() => {
    let timerInterval;

    if (isStartClicked) {
      timerInterval = setInterval(() => {
        setCountdownTime((prevTime) => {
          if (prevTime <= 0) {
            clearInterval(timerInterval);
            setStartClicked(false);
            console.log("Countdown stopped");

            // Send notification when countdown reaches 0
            if ("Notification" in window) {
              Notification.requestPermission().then((permission) => {
                if (permission === "granted") {
                  new Notification("Time is up!", {
                    body: "sen cok tatlisin laggnnnnn ",
                  });
                }
              });
            }
            switchToNextTimer();
            return 0;
          }
          return prevTime - 1;
        });
      }, 1000);
    }

    // Cleanup function to clear the interval when the component unmounts or when the timer reaches 0
    return () => {
      clearInterval(timerInterval);
    };
  }, [isStartClicked, switchToNextTimer, howManyPomo]);

  useEffect(() => {
    // Reset countdown time when the timer duration changes
    setCountdownTime(timer * 60);
  }, [timer]);
  const formatTime = (timeInSeconds) => {
    const minutes = Math.floor(timeInSeconds / 60);
    const seconds = timeInSeconds % 60;
    return `${minutes}:${seconds < 10 ? "0" : ""}${seconds}`;
  };
  const [whicButton, setWhichButton] = useState("Pomogulcan");
  return (
    <section className="header-container">
      <div className="header-box">
        <div className="navbar">
          <button className="left">
            <CheckCircleIcon />
            <span>Pomogulcan</span>
          </button>
          <div className="right">
            <div className="right-box">
              <BarChartIcon />
              <span>Report</span>
            </div>
            <div className="right-box">
              <SettingsApplicationsIcon />
              <span>Settings</span>
            </div>
            <div className="right-box">
              <AccountCircleIcon />
              <span>Login</span>
            </div>
          </div>
        </div>
        <div className="timer-box">
          <div className="timer-select">
            <button
              onClick={() => {
                setTimer(25);
                setCountdownTime(25 * 60);
                setWhichButton("Pomogulcan");
                setStartClicked(false);
              }}
              className={whicButton === "Pomogulcan" ? "make-background" : null}
            >
              Pomogulcan
            </button>
            <button
              className={
                whicButton === "Short Break" ? "make-background" : null
              }
              onClick={() => {
                setTimer(25);
                setCountdownTime(5 * 60);
                setWhichButton("Short Break");
                setStartClicked(false);
              }}
            >
              Short Break
            </button>
            <button
              className={whicButton === "Long Break" ? "make-background" : null}
              onClick={() => {
                setTimer(25);
                setCountdownTime(15 * 60);
                setWhichButton("Long Break");
                setStartClicked(false);
              }}
            >
              Long Break
            </button>
          </div>
          <div className="timer-count">
            <h1>{formatTime(countdownTime)}</h1>
          </div>
          <div className="timer-start-button">
            <button
              onClick={() => {
                setStartClicked((prevVal) => !prevVal);
              }}
            >
              {isStartClicked ? "STOP" : "START"}
            </button>
          </div>
        </div>
        <div className="timer-reset">
          <button onClick={() => setHowManyPomo(1)}>#{howManyPomo}</button>
          <h3>Time to focus!</h3>
        </div>
      </div>
    </section>
  );
}

export default Header;
